.App {
  text-align: center;
}

.center {
  display:flex;
  justify-content: center;

}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}